"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import Card from "@/components/ui/card";
import { authClient } from "@/lib/auth-client";
import Tabs from "@/components/ui/tabs";

export default () => {
	const [loading, setLoading] = useState<boolean>(false);
	return (
		<Card className="absolute top-1/2 left-1/2 w-full max-w-2xl -translate-x-1/2 -translate-y-1/2 md:max-w-1/2">
			<Card.Header>
				<Card.Title>Sign in using oauth</Card.Title>
			</Card.Header>
			<Card.Content className="flex min-w-sm flex-col gap-6">
				<div className="grid grid-cols-2 gap-6">
					<Github loading={loading} setLoading={setLoading} />
					<Google loading={loading} setLoading={setLoading} />
				</div>
				<div className="text-center">
					------------------------ Or ------------------------
				</div>
				<Tabs defaultValue="login">
					<Tabs.List className="grid grid-cols-2">
						<Tabs.Trigger value="login">Login</Tabs.Trigger>
						<Tabs.Trigger value="register">Register</Tabs.Trigger>
					</Tabs.List>
					<Tabs.Content value="login"></Tabs.Content>
					<Tabs.Content value="register"></Tabs.Content>
				</Tabs>
			</Card.Content>
		</Card>
	);
};

type Props = {
	loading: boolean;
	setLoading: (loading: boolean) => void;
};

const Google = ({ loading, setLoading }: Props) => {
	return (
		<Button
			onClick={async () => {
				setLoading(true);
				try {
					await authClient.signIn.social({
						provider: "google",
						callbackURL: "/dashboard",
					});
				} catch {}
				setLoading(false);
			}}
			disabled={loading}
		>
			Google
		</Button>
	);
};

const Github = ({ loading, setLoading }: Props) => {
	return (
		<Button
			onClick={async () => {
				setLoading(true);
				try {
					await authClient.signIn.social({
						provider: "github",
						callbackURL: "/dashboard",
					});
				} catch {}
				setLoading(false);
			}}
			disabled={loading}
		>
			GitHub
		</Button>
	);
};
